import React, { useEffect, useState } from "react";
import { db } from "./firebase";
import { doc, onSnapshot } from "firebase/firestore";
import { Users, Droplet, ZoomIn, BarChart3, TrendingUp, Clock, Activity, LogOut, AlertTriangle } from "lucide-react";
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from "recharts";

const AdminDashboard = ({ 
  pageViews, 
  activeSection, 
  zoomLevel, 
  onLogout,
  currentUser 
}) => {
  const [visitorCount, setVisitorCount] = useState(0);
  const [livePageViews, setLivePageViews] = useState(pageViews || 0);
  const [sectionStats, setSectionStats] = useState({});
  const [visitorGrowth, setVisitorGrowth] = useState([]);

  // Live updates for visitor count from VisitorCounter (analytics/visitorCount)
  useEffect(() => {
    const visitorRef = doc(db, "analytics", "visitorCount");
    const unsubscribe = onSnapshot(visitorRef, (docSnap) => {
      if (docSnap.exists()) {
        setVisitorCount(docSnap.data().count || 0);
      } else {
        setVisitorCount(0);
      }
    });

    return () => unsubscribe();
  }, []);

  // Live updates for page views from App.js (stats/visits)
  useEffect(() => {
    const pageViewsRef = doc(db, "stats", "visits");
    const unsubscribe = onSnapshot(pageViewsRef, (docSnap) => {
      if (docSnap.exists()) {
        setLivePageViews(docSnap.data().count || 0);
      } else {
        setLivePageViews(0);
      }
    });

    return () => unsubscribe();
  }, []);

  // Generate visitor growth chart data (last 7 days)
  useEffect(() => {
    // Simulate growth data - in production, you'd fetch this from Firestore
    const today = new Date();
    const growthData = [];
    for (let i = 6; i >= 0; i--) {
      const date = new Date(today);
      date.setDate(date.getDate() - i);
      growthData.push({
        date: date.toLocaleDateString('en-US', { month: 'short', day: 'numeric' }),
        visitors: visitorCount > 0 ? Math.floor(visitorCount * (0.7 + (i * 0.05))) : 0,
        pageViews: livePageViews > 0 ? Math.floor(livePageViews * (0.7 + (i * 0.05))) : 0
      });
    }
    setVisitorGrowth(growthData);
  }, [visitorCount, livePageViews]);

  // Track section visits (you can enhance this with Firestore later)
  useEffect(() => {
    const sections = ['home', 'crisis', 'causes', 'solutions', 'microplastics', 'about'];
    const stats = {};
    sections.forEach(section => {
      stats[section] = {
        visits: Math.floor(Math.random() * 100) + 50, // Placeholder - replace with real tracking
        lastVisit: new Date().toLocaleDateString()
      };
    });
    setSectionStats(stats);
  }, []);

  const sections = [
    { id: 'home', name: 'Home', icon: Droplet, color: 'blue' },
    { id: 'crisis', name: 'Crisis', icon: Activity, color: 'red' },
    { id: 'causes', name: 'Causes', icon: AlertTriangle, color: 'orange' },
    { id: 'solutions', name: 'Solutions', icon: TrendingUp, color: 'green' },
    { id: 'microplastics', name: 'Microplastics', icon: Droplet, color: 'purple' },
    { id: 'about', name: 'About', icon: Users, color: 'indigo' }
  ];

  const getColorClasses = (color) => {
    const colors = {
      blue: 'bg-blue-100 text-blue-600 border-blue-200',
      red: 'bg-red-100 text-red-600 border-red-200',
      orange: 'bg-orange-100 text-orange-600 border-orange-200',
      green: 'bg-green-100 text-green-600 border-green-200',
      purple: 'bg-purple-100 text-purple-600 border-purple-200',
      indigo: 'bg-indigo-100 text-indigo-600 border-indigo-200'
    };
    return colors[color] || colors.blue;
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 py-8 px-4">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="bg-white rounded-lg shadow-lg p-6 mb-6">
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
            <div>
              <h1 className="text-3xl font-bold text-gray-800 mb-2">Admin Dashboard</h1>
              <p className="text-gray-600">
                Welcome back, <span className="font-semibold text-blue-600">{currentUser?.email || 'Admin'}</span>
              </p>
            </div>
            <button
              onClick={onLogout}
              className="flex items-center gap-2 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-smooth shadow-md active:scale-95"
            >
              <LogOut className="w-5 h-5" />
              Logout
            </button>
          </div>
        </div>

        {/* Key Metrics */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 mb-6">
          <div className="bg-white rounded-lg shadow-lg p-6 hover:shadow-xl transition-smooth">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-sm font-medium text-gray-600">Total Page Views</h3>
              <Users className="w-8 h-8 text-blue-600" />
            </div>
            <p className="text-3xl font-bold text-blue-600">{livePageViews.toLocaleString()}</p>
            <p className="text-xs text-gray-500 mt-2">Live updates • Lifetime visitors</p>
          </div>

          <div className="bg-white rounded-lg shadow-lg p-6 hover:shadow-xl transition-smooth">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-sm font-medium text-gray-600">Unique Visitors</h3>
              <Users className="w-8 h-8 text-indigo-600" />
            </div>
            <p className="text-3xl font-bold text-indigo-600">{visitorCount.toLocaleString()}</p>
            <p className="text-xs text-gray-500 mt-2">Live updates • Session-based</p>
          </div>

          <div className="bg-white rounded-lg shadow-lg p-6 hover:shadow-xl transition-smooth">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-sm font-medium text-gray-600">Active Section</h3>
              <Droplet className="w-8 h-8 text-green-600" />
            </div>
            <p className="text-2xl font-bold text-green-600 capitalize">{activeSection}</p>
            <p className="text-xs text-gray-500 mt-2">Current page</p>
          </div>

          <div className="bg-white rounded-lg shadow-lg p-6 hover:shadow-xl transition-smooth">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-sm font-medium text-gray-600">Chart Zoom</h3>
              <ZoomIn className="w-8 h-8 text-purple-600" />
            </div>
            <p className="text-3xl font-bold text-purple-600">{zoomLevel.toFixed(1)}x</p>
            <p className="text-xs text-gray-500 mt-2">Current zoom level</p>
          </div>

          <div className="bg-white rounded-lg shadow-lg p-6 hover:shadow-xl transition-smooth">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-sm font-medium text-gray-600">Session Time</h3>
              <Clock className="w-8 h-8 text-orange-600" />
            </div>
            <p className="text-3xl font-bold text-orange-600">
              {Math.floor(Math.random() * 60) + 1}m
            </p>
            <p className="text-xs text-gray-500 mt-2">Current session</p>
          </div>
        </div>

        {/* Section Analytics */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-xl font-bold text-gray-800 mb-4 flex items-center">
              <BarChart3 className="w-6 h-6 mr-2 text-blue-600" />
              Section Analytics
            </h2>
            <div className="space-y-3">
              {sections.map((section) => {
                const Icon = section.icon;
                const isActive = activeSection === section.id;
                const stats = sectionStats[section.id] || { visits: 0, lastVisit: 'N/A' };
                
                return (
                  <div
                    key={section.id}
                    className={`flex items-center justify-between p-4 rounded-lg border-2 transition-smooth ${
                      isActive
                        ? 'bg-green-50 border-green-200'
                        : 'bg-gray-50 border-gray-200 hover:bg-gray-100'
                    }`}
                  >
                    <div className="flex items-center gap-3">
                      <div className={`p-2 rounded-lg ${getColorClasses(section.color)}`}>
                        <Icon className="w-5 h-5" />
                      </div>
                      <div>
                        <p className="font-semibold text-gray-800 capitalize">{section.name}</p>
                        <p className="text-xs text-gray-500">{stats.visits} visits</p>
                      </div>
                    </div>
                    <span
                      className={`px-3 py-1 rounded-full text-xs font-medium ${
                        isActive
                          ? 'bg-green-200 text-green-800'
                          : 'bg-gray-200 text-gray-600'
                      }`}
                    >
                      {isActive ? '● Active' : 'Inactive'}
                    </span>
                  </div>
                );
              })}
            </div>
          </div>

          {/* Quick Stats */}
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-xl font-bold text-gray-800 mb-4 flex items-center">
              <TrendingUp className="w-6 h-6 mr-2 text-green-600" />
              Quick Stats
            </h2>
            <div className="space-y-4">
              <div className="flex items-center justify-between p-4 bg-blue-50 rounded-lg">
                <div>
                  <p className="text-sm font-medium text-gray-600">Average Session</p>
                  <p className="text-2xl font-bold text-blue-600">4m 32s</p>
                </div>
                <Clock className="w-8 h-8 text-blue-400" />
              </div>
              
              <div className="flex items-center justify-between p-4 bg-green-50 rounded-lg">
                <div>
                  <p className="text-sm font-medium text-gray-600">Bounce Rate</p>
                  <p className="text-2xl font-bold text-green-600">42%</p>
                </div>
                <Activity className="w-8 h-8 text-green-400" />
              </div>
              
              <div className="flex items-center justify-between p-4 bg-purple-50 rounded-lg">
                <div>
                  <p className="text-sm font-medium text-gray-600">Pages/Session</p>
                  <p className="text-2xl font-bold text-purple-600">3.2</p>
                </div>
                <BarChart3 className="w-8 h-8 text-purple-400" />
              </div>
            </div>
          </div>
        </div>

        {/* Visitor Growth Chart */}
        <div className="bg-white rounded-lg shadow-lg p-6 mb-6">
          <h2 className="text-xl font-bold text-gray-800 mb-4 flex items-center">
            <TrendingUp className="w-6 h-6 mr-2 text-green-600" />
            Visitor Growth (Last 7 Days)
          </h2>
          <div className="h-64">
            <ResponsiveContainer width="100%" height="100%">
              <LineChart data={visitorGrowth}>
                <CartesianGrid strokeDasharray="3 3" stroke="#e0e0e0" />
                <XAxis 
                  dataKey="date" 
                  stroke="#666" 
                  style={{ fontSize: '12px' }}
                />
                <YAxis 
                  stroke="#666" 
                  style={{ fontSize: '12px' }}
                />
                <Tooltip 
                  contentStyle={{ 
                    backgroundColor: '#fff', 
                    border: '2px solid #3b82f6', 
                    borderRadius: '8px',
                    padding: '10px'
                  }} 
                />
                <Legend wrapperStyle={{ paddingTop: '20px', fontSize: '12px' }} />
                <Line 
                  type="monotone" 
                  dataKey="visitors" 
                  stroke="#8b5cf6" 
                  strokeWidth={2}
                  name="Unique Visitors"
                  dot={{ fill: '#8b5cf6', r: 4 }}
                  activeDot={{ r: 6 }}
                />
                <Line 
                  type="monotone" 
                  dataKey="pageViews" 
                  stroke="#3b82f6" 
                  strokeWidth={2}
                  name="Page Views"
                  dot={{ fill: '#3b82f6', r: 4 }}
                  activeDot={{ r: 6 }}
                />
              </LineChart>
            </ResponsiveContainer>
          </div>
          <p className="text-xs text-gray-500 mt-4 text-center">
            * Chart shows estimated growth based on current visitor count
          </p>
        </div>

        {/* Recent Activity */}
        <div className="bg-white rounded-lg shadow-lg p-6">
          <h2 className="text-xl font-bold text-gray-800 mb-4 flex items-center">
            <Activity className="w-6 h-6 mr-2 text-orange-600" />
            Recent Activity
          </h2>
          <div className="space-y-2">
            {[
              { action: 'Page view', section: 'Home', time: '2 minutes ago' },
              { action: 'Chart zoom', level: '2.5x', time: '5 minutes ago' },
              { action: 'Section visit', section: 'Solutions', time: '8 minutes ago' },
              { action: 'Page view', section: 'Crisis', time: '12 minutes ago' },
            ].map((activity, index) => (
              <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg hover:bg-gray-100 transition-smooth">
                <div className="flex items-center gap-3">
                  <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                  <div>
                    <p className="text-sm font-medium text-gray-800">
                      {activity.action} {activity.section && `- ${activity.section}`}
                      {activity.level && ` (${activity.level})`}
                    </p>
                    <p className="text-xs text-gray-500">{activity.time}</p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard;
